import {BrowserModule} from '@angular/platform-browser';
import {ErrorHandler, NgModule} from '@angular/core';
import {IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';
import {SplashScreen} from '@ionic-native/splash-screen';
import {StatusBar} from '@ionic-native/status-bar';

import {MyApp} from './app.component';
import {CountryProvider} from "../providers/country/country";
import {SelectCodePageModule} from "../pages/select-code/select-code.module";
import {HttpClient, HttpClientModule} from "@angular/common/http";
import {Api} from "../providers/api";
import {IonicStorageModule} from "@ionic/storage";
import {GeolocationProvider} from '../providers/geolocation/geolocation';
import {Geolocation} from "@ionic-native/geolocation";
import {NativeGeocoder} from "@ionic-native/native-geocoder";
import {AuthUserProvider} from '../providers/auth-user/auth-user';
import {RegNumberPhonePageModule} from "../pages/reg-number-phone/reg-number-phone.module";
import {VerificationProvider} from '../providers/verification/verification';
import {LoadInformationProvider} from '../providers/load-information/load-information';
import {GoogleMaps} from "@ionic-native/google-maps";
import {ValidationProvider} from '../providers/validation/validation';
import {FormatterDateProvider} from '../providers/formatter-date/formatter-date';
import {Clipboard} from "@ionic-native/clipboard";
import {MapProvider} from '../providers/map/map';
import {CurrencyPipe} from "@angular/common";
import {Contacts} from "@ionic-native/contacts";
import {TranslateHttpLoader} from "@ngx-translate/http-loader";
import {TranslateLoader, TranslateModule} from "@ngx-translate/core";
import {LenguageProvider} from '../providers/lenguage/lenguage';
import {ModalErrorProvider} from "../providers/modal-error/modal-error";


export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}


@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/account/account.module.ngfactory#AccountPageModuleNgFactory', name: 'AccountPage', segment: 'account', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/contacts/contacts.module.ngfactory#ContactsPageModuleNgFactory', name: 'ContactsPage', segment: 'contacts', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/history-resume/history-resume.module.ngfactory#HistoryResumePageModuleNgFactory', name: 'HistoryResumePage', segment: 'history-resume', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'app', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/history/history.module.ngfactory#HistoryPageModuleNgFactory', name: 'HistoryPage', segment: 'history', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/map/map.module.ngfactory#MapPageModuleNgFactory', name: 'MapPage', segment: 'map', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/modal-error/modal-error.module.ngfactory#ModalErrorPageModuleNgFactory', name: 'ModalErrorPage', segment: 'modal-error', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/modal-transaction/modal-transaction.module.ngfactory#ModalTransactionPageModuleNgFactory', name: 'ModalTransactionPage', segment: 'modal-transaction', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/modal-welcome/modal-welcome.module.ngfactory#ModalWelcomePageModuleNgFactory', name: 'ModalWelcomePage', segment: 'modal-welcome', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/password-update/password-update.module.ngfactory#PasswordUpdatePageModuleNgFactory', name: 'PasswordUpdatePage', segment: 'password-update', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/recovery-password/recovery-password.module.ngfactory#RecoveryPasswordPageModuleNgFactory', name: 'RecoveryPasswordPage', segment: 'recovery-password', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/reg-number-phone/reg-number-phone.module.ngfactory#RegNumberPhonePageModuleNgFactory', name: 'RegNumberPhonePage', segment: 'reg-number-phone', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/select-code/select-code.module.ngfactory#SelectCodePageModuleNgFactory', name: 'SelectCodePage', segment: 'select-code', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/transaction-resume/transaction-resume.module.ngfactory#TransactionResumePageModuleNgFactory', name: 'TransactionResumePage', segment: 'transaction-resume', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/transaction/transaction.module.ngfactory#TransactionPageModuleNgFactory', name: 'TransactionPage', segment: 'transaction', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/language/language.module.ngfactory#LanguagePageModuleNgFactory', name: 'LanguagePage', segment: 'language', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/security/security.module.ngfactory#SecurityPageModuleNgFactory', name: 'SecurityPage', segment: 'security', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/politicas/politicas.module.ngfactory#PoliticasPageModuleNgFactory', name: 'PoliticasPage', segment: 'politicas', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot(),
    SelectCodePageModule,
    RegNumberPhonePageModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    CountryProvider,
    Api,
    Geolocation,
    NativeGeocoder,
    {provide: ErrorHandler, useClass: IonicErrorHandler,},
    GeolocationProvider,
    AuthUserProvider,
    VerificationProvider,
    LoadInformationProvider,
    GoogleMaps,
    ValidationProvider,
    FormatterDateProvider,
    Clipboard,
    MapProvider,
    CurrencyPipe,
    Contacts,
    LenguageProvider,
    ModalErrorProvider,
  ]
})
export class AppModule {
}



